import { Constants } from "@/constants";
import console from "@/util/console";

export class SettingsService {
  public actionAtEnd: string;
  public timeRemaning: number;
  public soundOn = false;
  public changeColorOn = true;
  public countsBeforeAds: number;
  public timesPerMinute = 160;
  public accentBetweenTicks = 4;
  public fontSize: string;

  private static _instance: SettingsService;
  public static getInstance(): SettingsService {
    if (
      !SettingsService._instance &&
      localStorage?.getItem("settings") === "1"
    ) {
      console.log("settings - getInstance - localStorage");
      SettingsService._instance = new SettingsService();
      SettingsService._instance.actionAtEnd =
        localStorage.getItem("settings.actionAtEnd") ??
        Constants.ActionsAtEnd.DEFAULT;
      SettingsService._instance.timeRemaning = parseInt(
        localStorage.getItem("settings.timeRemaning") ??
          SettingsService._instance.timeRemaning + "",
        10
      );
      console.log("settings - getInstance -  ls ", {
        a: localStorage.getItem("settings.timeRemaning"),
        b: SettingsService._instance.timeRemaning,
      });
      SettingsService._instance.soundOn =
        (localStorage.getItem("settings.soundOn") ?? "false") === "true";
      SettingsService._instance.changeColorOn =
        (localStorage.getItem("settings.changeColorOn") ?? "false") === "true";
      SettingsService._instance.countsBeforeAds =
        parseInt(
          localStorage.getItem("settings.countsBeforeAds") ??
            Constants.COUNTS_BEFORE_ADS.toString(),
          10
        ) ?? Constants.COUNTS_BEFORE_ADS;
      SettingsService._instance.timesPerMinute =
        parseInt(
          localStorage.getItem("settings.timesPerMinute") ??
            Constants.TIMES_PER_MINUTE_DEFAULT.toString(),
          10
        ) ?? Constants.TIMES_PER_MINUTE_DEFAULT;
      SettingsService._instance.accentBetweenTicks =
        parseInt(
          localStorage.getItem("settings.accentBetweenTicks") ??
            Constants.ACCENT_BETWEEN_TICKS_DEFAULT.toString(),
          10
        ) ?? Constants.ACCENT_BETWEEN_TICKS_DEFAULT;
      SettingsService._instance.fontSize =
        localStorage.getItem("settings.fontSize") ??
        Constants.FONT_SIZE_DEFAULT;
    }
    if (!SettingsService._instance) {
      console.log("settings - getInstance - new");
      SettingsService._instance = new SettingsService();
      SettingsService._instance.saveInLocalStorage();
    }
    return SettingsService._instance;
  }

  private constructor() {
    console.log("settings - constructor");
    this.actionAtEnd = Constants.ActionsAtEnd.DEFAULT;
    this.timeRemaning = Constants.TIMEOUT_SECONDS;
    this.countsBeforeAds = Constants.COUNTS_BEFORE_ADS;
    this.timesPerMinute = Constants.TIMES_PER_MINUTE_DEFAULT;
    this.accentBetweenTicks = Constants.ACCENT_BETWEEN_TICKS_DEFAULT;
    this.fontSize = Constants.FONT_SIZE_DEFAULT;
  }

  private saveInLocalStorage() {
    console.log("settings - saveInLocalStorage");
    localStorage?.setItem("settings", "1");
    localStorage?.setItem(
      "settings.actionAtEnd",
      SettingsService._instance.actionAtEnd.toString()
    );
    localStorage?.setItem(
      "settings.timeRemaning",
      SettingsService._instance.timeRemaning.toString()
    );
    localStorage?.setItem(
      "settings.soundOn",
      SettingsService._instance.soundOn.toString()
    );
    localStorage?.setItem(
      "settings.changeColorOn",
      SettingsService._instance.changeColorOn.toString()
    );
    localStorage?.setItem(
      "settings.countsBeforeAds",
      SettingsService._instance.countsBeforeAds.toString()
    );
    localStorage?.setItem(
      "settings.timesPerMinute",
      SettingsService._instance.timesPerMinute.toString()
    );
    localStorage?.setItem(
      "settings.accentBetweenTicks",
      SettingsService._instance.accentBetweenTicks.toString()
    );
    localStorage?.setItem(
      "settings.fontSize",
      SettingsService._instance.fontSize.toString()
    );
  }

  setTimeout(seconds: number): void {
    this.timeRemaning = seconds;
    console.log("settings - this.timeout", this.timeRemaning);
    localStorage?.setItem(
      "settings.timeRemaning",
      SettingsService._instance.timeRemaning.toString()
    );
  }

  setActionAtEnd(action: string): void {
    this.actionAtEnd = action;
    console.log("settings - this.actionAtEnd", this.actionAtEnd);
    localStorage?.setItem(
      "settings.actionAtEnd",
      SettingsService._instance.actionAtEnd.toString()
    );
  }

  setSoundOn(soundOn: boolean): void {
    this.soundOn = soundOn;
    console.log("settings - this.soundOn", this.soundOn);
    localStorage?.setItem(
      "settings.soundOn",
      SettingsService._instance.soundOn.toString()
    );
  }

  setChangeColorOn(changeColorOn: boolean): void {
    this.changeColorOn = changeColorOn;
    console.log("settings - this.changeColorOn", this.changeColorOn);
    localStorage?.setItem(
      "settings.changeColorOn",
      SettingsService._instance.changeColorOn.toString()
    );
  }

  setTimesPerMinute(timesPerMinute: number): void {
    this.timesPerMinute = timesPerMinute;
    console.log("settings - this.timesPerMinute", this.timesPerMinute);
    localStorage?.setItem(
      "settings.timesPerMinute",
      SettingsService._instance.timesPerMinute.toString()
    );
  }

  setCountsBeforeAds(countsBeforeAds: number): void {
    this.countsBeforeAds = countsBeforeAds;
    console.log("settings - this.countsBeforeAds", this.countsBeforeAds);
    localStorage?.setItem(
      "settings.countsBeforeAds",
      SettingsService._instance.countsBeforeAds.toString()
    );
  }

  setAccentBetweenTicks(accentBetweenTicks: number): void {
    this.accentBetweenTicks = accentBetweenTicks;
    console.log("settings - this.accentBetweenTicks", this.accentBetweenTicks);
    localStorage?.setItem(
      "settings.accentBetweenTicks",
      SettingsService._instance.accentBetweenTicks.toString()
    );
  }

  setFontSize(fontSize: string): void {
    this.fontSize = fontSize;
    console.log("settings - this.fontSize", this.fontSize);
    localStorage?.setItem(
      "settings.fontSize",
      SettingsService._instance.fontSize.toString()
    );
  }
}

export const settings = SettingsService;
