















import { Component, Prop, Vue } from "vue-property-decorator";
import VueMarkdown from "vue-markdown-render";

@Component({
  components: {
    VueMarkdown,
  },
})
export default class ExpressDefinition extends Vue {
  @Prop({ default: "" }) definition!: string;
  @Prop({ default: true }) isHtml!: boolean;
  @Prop({ default: false }) showDefinition!: boolean;
  options = {
    html: true,
  };

  get classDefined(): string {
    return this.showDefinition
      ? " "
      : "hidden " + this.isHtml
      ? "definition-container"
      : "";
  }
}
