import Vue from "vue";

Vue.filter("formatDate", function (value: Date | string) {
  if (value) {
    // format date whth the format DD/MM/YYYY hh:mm
    return new Date(value).toLocaleDateString("es", {
      year: "numeric",
      month: "2-digit",
      day: "2-digit",
      hour: "2-digit",
      minute: "2-digit",
    });
  }
});
