import { Word } from "@/types/word";
import { Constants } from "@/constants";
import console from "@/util/console";

const local = localStorage;
const SAVED_WORDS_KEY = "saved_words";
const CUSTOM_LIST_KEY = "saved_words";
const EMPTY_VALUE = "[]";

function add(newWord: Word): void {
  if (
    newWord.Word === Constants.END_OF_WORDS_TEXT ||
    newWord.Word === Constants.START_OF_WORDS_TEXT
  ) {
    return;
  }

  let data = local[SAVED_WORDS_KEY];
  if (!data) {
    data = EMPTY_VALUE;
  }

  const words: Word[] = JSON.parse(data);
  const exists = words.filter((x) => x.Word === newWord.Word).length > 0;
  if (!exists) {
    words.push(newWord);
    local[SAVED_WORDS_KEY] = JSON.stringify(words);
    console.log("localstorage - word added", newWord);
  } else {
    console.log("localstorage - word exist", newWord);
  }
}

function remove(newWord: Word): void {
  let data = local[SAVED_WORDS_KEY];
  if (!data) {
    data = EMPTY_VALUE;
  }
  const words: Word[] = JSON.parse(data);
  const newWords = words.filter((x) => x.Word !== newWord.Word);
  local[SAVED_WORDS_KEY] = JSON.stringify(newWords);
  console.log("localstorage - word deleted", newWord);
}

function getAll(): Word[] {
  let data = local[SAVED_WORDS_KEY];
  if (!data) {
    data = EMPTY_VALUE;
  }
  const words: Word[] = JSON.parse(data);
  console.log("localstorage - getAll", words);
  return words;
}

function setCustomList(words: Word[]): void {
  local[CUSTOM_LIST_KEY] = JSON.stringify(words);
  console.log("localstorage - set custom list", words.length);
}

function getCustomList(): Word[] {
  const data = local[CUSTOM_LIST_KEY];
  const words: Word[] = JSON.parse(data);
  console.log("localstorage - getAll", words);
  return words;
}

