import console from "@/util/console";

export function serialize<T>(obj: T): string {
  console.log("serialize - input", obj);
  const str: string[] = [];
  if (typeof obj !== "undefined") {
    const keys = Object.keys(obj);
    for (const p of keys) {
      if (p) {
        if (obj[p] !== undefined) {
          str.push(`${p}=${obj[p]}`);
        }
      }
    }
  }
  const output = str.join("&");
  console.log("serialize - output", output);
  return output;
}
