import { Word } from "@/types/word";
import { APIOwner, APIResponse } from "@/types/api-response";
// import axios from "axios";
import { Nullable } from "@/types/nullable";
import { Comment } from "@/types/comment";
import { serialize } from "@/util/seriealize";
import { Name } from "@/types/name";
import { Onomatopeia } from "@/types/onomatopeia";
import { Animal } from "@/types/animal";
import console from "@/util/console";
import { Stats } from "@/types/stats";


const api = process.env.VUE_APP_API;
const apiRandomName = process.env.VUE_APP_RANDOM_NAME_API;
const MAX_USES_CACHE = 500;
const PAGE_SIZE = 10;
const START_PAGE = 0;

let cache: {
  hasValue: boolean;
  words: Word[];
  api_owner: Nullable<APIOwner>;
  usesCounter: number;
};

const cacheStr = localStorage.getItem("cache");
if (cacheStr && cacheStr !== "") {
  cache = JSON.parse(cacheStr);
} else {
  cache = {
    hasValue: false,
    words: [],
    api_owner: null,
    usesCounter: 0,
  };
}

async function apiGet<T>(url: string): Promise<T> {
  return await fetch(url, { mode: "cors" })
    .then(function (response) {
      return response.json();
    })
    .then(function (data) {
      console.log("Request successful", data);
      return data;
    })
    .catch(function (error) {
      console.error("Request failed", error);
      return null;
    });
}

function extractFirstWord(response: APIResponse<Word[]>): APIResponse<Word> {
  console.log("api extract first word and set it into cache");
  cache.api_owner = response.api_owner;
  cache.words = response.body || [];
  cache.hasValue = cache.words.length > 0;
  localStorage.setItem("cache", JSON.stringify(cache));
  console.log("api cache updated", cache);
  return <APIResponse<Word>>{
    api_owner: response.api_owner,
    body: response.body[0] || ({} as Word),
  };
}

async function multipleRandomWords(): Promise<APIResponse<Word[]>> {
  return await apiGet<APIResponse<Word[]>>(`${api}/multiple-random`);
}

async function singleRandomWords(): Promise<APIResponse<Word>> {
  return await apiGet<APIResponse<Word>>(`${api}/random`);
}

async function getRandomFromServer(): Promise<APIResponse<Word>> {
  console.log("api - get word from server");
  return await Promise.race([
    singleRandomWords(),
    multipleRandomWords().then(extractFirstWord),
  ]);
}

async function getRandomWordFromCache(): Promise<APIResponse<Word>> {
  console.log("api - get word from cache");
  return await new Promise((resolve) => {
    const max = cache.words.length - 1;
    const randomIndex = Math.floor(Math.random() * max);
    const word = cache.words[randomIndex];
    resolve(<APIResponse<Word>>{
      api_owner: cache.api_owner,
      body: word,
    });
  });
}

async function randomWord(): Promise<APIResponse<Word>> {
  console.log("api - get random - cache.hasValue=", {
    hasValue: cache.hasValue,
    usesCounter: cache.usesCounter,
  });
  if (cache.hasValue) {
    cache.usesCounter++;
    if (cache.usesCounter > MAX_USES_CACHE) {
      return getRandomFromServer();
    } else {
      return getRandomWordFromCache();
    }
  } else {
    return getRandomFromServer();
  }
}

async function get(word: string): Promise<APIResponse<Word[]>> {
  return await apiGet<APIResponse<Word[]>>(
    `${api}/palabras-aleatorias?Word=${word}`
  );
}

async function addComment(comment: Comment): Promise<APIResponse<Word>> {
  const params = serialize(comment);
  console.log({ params });
  return await apiGet<APIResponse<Word>>(`${api}/add-word?${params}`);
}

async function getComments(): Promise<APIResponse<Comment[]>> {
  return await apiGet<APIResponse<Comment[]>>(`${api}/comments`);
}

async function search(text: string): Promise<APIResponse<Word[]>> {
  return await apiGet<APIResponse<Word[]>>(
    `${api}/palabras-aleatorias?_page=${START_PAGE}&_limit=${PAGE_SIZE}&Word_like=${text}`
  );
}

async function randomName(): Promise<APIResponse<Name>> {
  return await apiGet<APIResponse<Name>>(`${apiRandomName}/random`);
}

async function randomOnomatopeia(): Promise<APIResponse<Onomatopeia>> {
  return await apiGet<APIResponse<Onomatopeia>>(`${api}/onomatopeia/random`);
}

async function randomAnimal(): Promise<APIResponse<Animal>> {
  return await apiGet<APIResponse<Animal>>(`${api}/animal/random`);
}

async function wordOfTheDay(): Promise<APIResponse<Word>> {
  return await apiGet<APIResponse<Word>>(`${api}/word-of-the-day`);
}

async function getStats(): Promise<APIResponse<Stats>> {
  return await apiGet<APIResponse<Stats>>(`${api}/stats`);
}

async function subscribeNewsletter(email: string): Promise<APIResponse<Stats>> {
  return await apiGet<APIResponse<Stats>>(
    `${api}/newsletter/subscribe?email=${email}`
  );
}

export default {
  randomWord: randomWord,
  get: get,
  addComment: addComment,
  getComments: getComments,
  search: search,
  randomName: randomName,
  randomOnomatopeia: randomOnomatopeia,
  randomAnimal: randomAnimal,
  wordOfTheDay: wordOfTheDay,
  getStats: getStats,
  subscribeNewsletter: subscribeNewsletter,
};
